<template>
  <div class="searchPage">
    <div class="topNav">
      <div class="navContent" @click="$router.push('/search')">
        <input
          type="text"
          class="iconfont"
          v-model="searchHistory"
          :placeholder="'&#xe626;' + placeholder"
          @keydown.enter="saveHistory"
        />
      </div>
      <div class="navRight" @click="$router.go(-1)">
        <span>取消</span>
      </div>
    </div>
    <div class="history">
      <p>历史</p>
      <div class="historyList">
        <div
          class="historyItem"
          v-for="(item, index) in historyList"
          :key="index"
          @click="historySearch(item)"
        >{{ item }}</div>
      </div>
      <div class="iconfont icon-lajitong" @click="emptyHistory"></div>
    </div>
    <!-- 歌手模块 -->
    <div class="singerModule" v-if="searchSong.length !== 0">
      <p class="singer">歌手</p>
      <div class="singerItem" v-for="(item, index) in serachSinger" :key="index">
        <div @click="toSingerView(item)">
          <div class="imgs">
            <el-image :src="item.img1v1Url" alt lazy />
            <img class="small" v-if="item.identityIconUrl" :src="item.identityIconUrl" alt />
          </div>
          <div class="right">
            <p>{{ item.name }}</p>
            <div :class="item.followed ? 'wd isFollowed' : 'wd NotFollowed'">
              <span
                :class="
                  item.followed ? 'iconfont icon-duigou' : 'iconfont icon-plus'
                "
              ></span>
              <span v-if="item.followed">已关注</span>
              <span v-else>关注</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <play-list-view
      :playlist="searchSong"
      :num="50"
      :playContent="'相关歌曲'"
      v-if="searchSong.length !== 0"
    />
  </div>
</template>
<script>
import { searchMusic, searchSinger, getArtist } from "@/tools/request"
import { ElMessageBox } from "element-plus"
import { mapState, mapMutations } from "vuex"

import playListView from "@/components/ListView/playListView"

export default {
  name: "Search",
  components: {
    playListView
  },
  data () {
    return {
      placeholder: "搜索音乐、视频、博客、歌词",
      historyList: [],
      searchHistory: "",
      searchSong: [],
      serachSinger: [],
    }
  },
  beforeMount () {
    this.historyList = localStorage.historyList
      ? JSON.parse(localStorage.historyList)
      : []
  },
  computed: {
    ...mapState([
      "showList",
      "playList",
      "playCurrentIndex",
      "isPause",
      "isShow",
      "nowPlay",
    ]),
  },
  methods: {
    emptyHistory () {
      ElMessageBox.confirm("确定要清空历史记录吗？", {
        confirmButtonText: "清空",
        confirmButtonClass: "confirm",
        cancelButtonText: "取消",
        type: "info ",
      })
        .then(() => {
          localStorage.removeItem("historyList")
          this.historyList = []
        })
        .catch(() => { })
    },
    async saveHistory () {
      if (this.searchHistory !== "") {
        this.historyList.unshift(this.searchHistory)
        this.historyList = Array.from(new Set(this.historyList))
        localStorage.setItem("historyList", JSON.stringify(this.historyList))
        let result = await searchMusic(this.searchHistory)
        let resultSinger = await searchSinger(this.searchHistory)
        this.serachSinger = resultSinger.data.result.artists.slice(0, 3)
        this.searchSong = result.data.result.songs
        this.setPlaylist(this.searchSong)
        this.searchHistory = ""
      }
    },
    historySearch (keyword) {
      this.searchHistory = keyword
      this.saveHistory()
    },
    // 点击去到歌手页面
    toSingerView (value) {
      this.$store.commit("clearSingerDetailList")
      this.$store.dispatch("reqArtist", value.id)
      this.$store.dispatch("reqArtistSong", value.id)
      this.$router.push("/singer")
    },
    ...mapMutations(["setPlayIndex", "setPlaylist", "setAllPlay"]),
  },
}
</script>

<style lang="less" scoped>
/deep/.el-image {
  display: block;
  overflow: visible;
}
/deep/.el-image__inner {
  width: 0.9rem;
  height: 0.9rem;
  border-radius: 50%;
}
.searchPage {
  width: 7.5rem;
  min-height: 100vh;
  padding: 0 0 0.2rem 0;
  background-color: #fff;
  .confirm {
    color: red !important;
  }
  .topNav {
    position: fixed;
    top: 0;
    left: 0;
    width: 7.5rem;
    height: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.2rem;
    background-color: #fff;
    z-index: 3;
    .navContent {
      flex: 9;
      display: flex;
      justify-content: space-around;
      align-items: center;
      padding: 0 0.1rem;
      input {
        width: 100%;
        padding: 0.25rem 0.2rem;
        border-radius: 0.4rem;
        border: none;
        outline: none;
        font-size: 14px;
        background-color: #f6f6f6;
      }
      input::-webkit-input-placeholder {
        color: rgb(145, 145, 145);
      }
    }
    .navRight {
      flex: 1;
      span {
        font-size: 0.3rem;
        font-weight: bold;
        color: #333;
      }
    }
  }
  .history {
    width: 7.5rem;
    height: 1rem;
    display: flex;
    align-items: center;
    padding: 1.2rem 0.3rem 0 0.3rem;
    p {
      flex: 1;
      font-size: 0.25rem;
      font-weight: bold;
      color: #333;
    }
    .iconfont {
      flex: 1;
      display: flex;
      justify-content: center;
      font-size: 0.4rem;
      color: #999;
    }
    .historyList {
      flex: 8;
      display: flex;
      align-items: center;
      white-space: nowrap;
      overflow-x: scroll;
      overflow-y: visible;
      &::-webkit-scrollbar {
        display: none;
      }
      .historyItem {
        font-size: 0.23rem;
        background-color: #f7f7f7;
        padding: 5px 12px;
        border-radius: 30px;
        margin: 0 5px;
      }
    }
  }
  .singerModule {
    padding-top: 0.4rem;
    width: 7.5rem;
    .singer {
      width: 7.5rem;
      padding: 0 0.3rem;
      font-size: 15px;
      font-weight: bold;
    }
    .singerItem {
      height: 0.8rem;
      margin: 0.4rem 0.3rem 0 0.3rem;
      .imgs {
        position: relative;
        display: inline-block;
        width: 0.9rem;
        height: 0.9rem;
        img {
          width: 0.9rem;
          height: 0.9rem;
          border-radius: 50%;
        }
        .small {
          position: absolute;
          bottom: 0;
          right: 0;
          width: 0.3rem;
          height: 0.3rem;
          border-radius: 50%;
        }
      }
      .right {
        width: 6rem;
        position: relative;
        top: -40px;
        left: 55px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-bottom: 0.4rem;
        margin-left: 0.1rem;
        border-bottom: 1px solid #eee;
        p {
          font-size: 0.27rem;
        }
        .wd {
          font-size: 0.22rem;
          padding: 3px 8px;
          border-radius: 20px;
          margin-right: 0.3rem;
        }
        .isFollowed {
          border: 1px solid rgb(219, 219, 219);
          color: rgb(219, 219, 219);
          padding: 3px 4px !important;
        }
        .NotFollowed {
          border: 1px solid #ea443b;
          color: #ea443b;
        }
        .iconfont {
          font-size: 0.25rem;
        }
        .icon-plus {
          display: inline-block;
          transform: scale(0.7);
        }
      }
    }
    .singerItem:nth-child(2) {
      margin: 0.2rem 0.3rem 0 0.3rem !important;
    }
  }
  .playListView {
    margin-top: 0.5rem;
    background-color: #fff;
  }
  .playListTop {
    height: 0.8rem;
    // position: fixed;
    // top: 1rem;
    // left: 0;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .left {
      display: flex;
      align-items: flex-end;
      svg {
        color: #ea443b;
        width: 0.4rem;
        height: 0.4rem;
      }
      p {
        font-size: 15px;
        font-weight: bold;
        margin: 0 0.1rem 0 0.15rem;
      }
      span {
        font-size: 12px;
        color: #999;
      }
    }
    .right {
      flex: 1;
      display: flex;
      align-items: flex-end;
      justify-content: flex-end;
      span {
        font-size: 0.33rem;
        margin: 0 0.1rem 0 0.4rem;
      }
    }
  }
  .playList {
    .playListItem:last-child {
      margin: 0.4rem 0 !important;
    }
    .playListItem:first-child {
      margin-top: 0.1rem;
    }
    .playListItem {
      display: flex;
      align-items: center;
      margin-top: 0.4rem;
      .serialNumber {
        color: #999;
        padding-right: 0.2rem;
        display: flex;
        flex: 1;
        justify-content: center;
      }
      .detail {
        flex: 15;
        .name {
          max-width: 260px;
          font-size: 0.28rem;
          color: #333;
          overflow: hidden;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
        }
        .author {
          font-size: 12px;
          color: #999;
          white-space: normal;
          overflow: hidden;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
        }
      }
      .rightIcon {
        color: #999;
        span {
          margin: 0 0.1rem 0 0.3rem;
        }
      }
    }
  }
}
</style>
